import { useDispatch, useSelector } from 'react-redux';
import styles from './type-input.module.css'
import {changeType} from '../../services/reducers/addPlacemarkForm'

function TypeInput() {
  const dispatch = useDispatch()
  const type = useSelector((state) => state.addPlacemarkForm.type)

  const setType = (value) => {
    dispatch(changeType(value))
  }

  return (
    <div className={styles.input}>
    <p className={styles.input__title}></p>
    <div className={styles.input__fields}>
        <div className={`${styles.input__field} ${type===''?styles.active:''}`} onClick={()=>setType('')} >
            <p className={`${styles.input__text} ${type===''?styles.active:''}`}></p>
        </div>
        <div className={`${styles.input__field} ${type===''?styles.active:''}`} onClick={()=>setType('')}>
            <p className={`${styles.input__text} ${type===''?styles.active:''}`}></p>
        </div>
    </div>
</div>
  );
}

export default TypeInput;